<template>
  <div class="home">
    <div class="chart-wrap" id="chart_wrap">
      <Charts></Charts>

      <!-- <canvas id="canvas">你的浏览器不支持canvas...</canvas> -->
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import Charts from "../components/Charts";
export default {
  name: "home",
  components: {
    Charts
  },
  data() {
    return {};
  },
  methods: {
    toggleBackground() {
      let chartWrap = document.getElementById("chart_wrap");

      let randomNumber = this.randomNum(1, 4);
      // console.log(randomNumber);

      if (randomNumber == 1) {
        chartWrap.className = "first-bgcolor";
      } else if (randomNumber == 2) {
        chartWrap.className = "second-bgcolor";
      } else if (randomNumber == 3) {
        chartWrap.className = "thred-bgcolor";
      } else {
        chartWrap.className = "four-bgcolor";
      }
    },
    //生成从minNum到maxNum的随机数
    randomNum(minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          break;
        default:
          return 0;
          break;
      }
    }
  },
  mounted() {
    let _this = this;
    // this.toggleBackground()
    // console.log(this.randomNum(1, 4));
    setInterval(function() {
      _this.toggleBackground();
    }, 5000);
  }
};
</script>

<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100vh;
}
#chart_wrap {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 0.8s;
  transition-timing-function: ease-in-out;
  background-color: #f4d03f;
  background-image: linear-gradient(132deg, #f4d03f 0%, #16a085 100%);

  &.first-bgcolor {
    background-color: #08aeea;
    background-image: linear-gradient(0deg, #08aeea 0%, #2af598 100%);
  }
  &.second-bgcolor {
    background-color: #3eecac;
    background-image: linear-gradient(19deg, #3eecac 0%, #ee74e1 100%);
  }
  &.thred-bgcolor {
    background-color: #faaca8;
    background-image: linear-gradient(19deg, #faaca8 0%, #ddd6f3 100%);
  }
  &.four-bgcolor {
    background-color: #fee140;
    background-image: linear-gradient(90deg, #fee140 0%, #fa709a 100%);
  }

  #canvas {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}
</style>
