<template>
  <div class="container">
    <div class="chart-container">
      <div id="chart"></div>
      <div class="btn-wrap" title="导出图片" @click="exportSvg">
        <svg
          t="1573045480925"
          class="icon"
          viewBox="0 0 1280 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="1135"
          width="28"
          height="28"
        >
          <path
            d="M526 780H380.8c-19.2 0-34.8-15.6-34.8-34.8V267.8c0-19.2 15.6-34.8 34.8-34.8h477.4c19.2 0 34.8 15.6 34.8 34.8V413c0 18.8 15.2 34 34 34s34-15.2 34-34V234.1c0-38.2-31-69.1-69.1-69.1H347.1c-38.2 0-69.1 31-69.1 69.1v544.7c0 38.2 31 69.1 69.1 69.1H526c18.8 0 34-15.2 34-34 0-18.7-15.2-33.9-34-33.9z"
            p-id="1136"
            fill="#333333"
          />
          <path
            d="M950.9 654.8l-0.1-0.1L817.1 521c-13.3-13.3-34.8-13.3-48.1 0-13.3 13.3-13.3 34.8 0 48.1l75.9 75.9H632c-18.8 0-34 15.2-34 34s15.2 34 34 34h212.7L769 788.7c-13.3 13.3-13.3 34.8 0 48.1 13.3 13.3 34.8 13.3 48.1 0l131.6-131.6c7.5-6.2 12.3-15.6 12.3-26.1 0-9.6-3.9-18.1-10.1-24.3z"
            fill="#333333"
            p-id="1137"
          />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import HighCharts from "highcharts";
import loadExporting from "highcharts/modules/exporting";

export default {
  data() {
    return {
      options: {
        title: {
          text: "薪资曲线表"
        },
        yAxis: {
          title: {
            text: "薪资（元）"
          }
        },
        xAxis: {
          categories: [
            "一月",
            "二月",
            "三月",
            "四月",
            "五月",
            "六月",
            "七月",
            "八月",
            "九月",
            "十月",
            "十一月",
            "十二月"
          ]
        },
        // legend: {
        //   layout: "vertical",
        //   align: "right",
        //   verticalAlign: "middle"
        // },
        plotOptions: {
          line: {
            dataLabels: {
              // 开启数据标签
              enabled: true
            },
            // 关闭鼠标跟踪，对应的提示框、点击事件会失效
            enableMouseTracking: true
          }
        },
        series: [
          {
            name: "2019年薪资（元）",
            data: [
              5197,
              3803,
              6395,
              6288,
              5124,
              4476,
              6916,
              10602,
              9128,
              6495.5,
              0,
              0
            ]
          },
          {
            name: "2018年薪资（元）",
            data: [0, 0, 0, 0, 0, 0, 0, 1192, 3250, 4000, 4000, 4365]
          }
        ],
        responsive: {
          rules: [
            {
              condition: {
                maxWidth: 500
              },
              chartOptions: {
                legend: {
                  layout: "horizontal",
                  align: "center",
                  verticalAlign: "bottom"
                }
              }
            }
          ]
        },
        exporting: {
          enabled: true,
          buttons: {
            contextButton: {
              // 自定义导出菜单项目及顺序
              menuItems: [
                {
                  text: "下载 PDF 文件",
                  onclick: function() {
                    this.exportChart({
                      type: "application/pdf"
                    });
                  }
                }
              ]
            }
          }
        }
      },
      imgUrl: "",
      fileName: '薪资'
    };
  },
  methods: {
    exportSvg() {
      let svgDom = document.getElementsByClassName("highcharts-root")[0];
      // console.log(document.getElementsByClassName('highcharts-root')[0])

      var svgString = new XMLSerializer().serializeToString(svgDom);
      // console.log(svgString)

      let svgBlob = new Blob([svgString], {
        type: "image/svg+xml;charset=utf-8"
      });
      var DOMURL = self.URL || self.webkitURL || self;
      // console.log("======DOMURL======", DOMURL)
      var url = DOMURL.createObjectURL(svgBlob);

      // var img = new Image(),
      //   canvas = document.createElement('canvas')

      // img.url = url

      // var ctx = canvas.getContext('2d')
      // ctx.drawImage(img, 0, 0)

      // console.log(url)
      var dlLink = document.createElement("a");
      dlLink.download = this.fileName;
      dlLink.href = url;
      dlLink.dataset.downloadurl = [
        'svg',
        dlLink.download,
        dlLink.href
      ].join(":");

      document.body.appendChild(dlLink);
      dlLink.click();
      document.body.removeChild(dlLink);
    }
  },
  mounted() {
    HighCharts.chart("chart", this.options);
  }
};
</script>
<style lang="scss" scoped>
