  position: relative;
  #chart {
    border-radius: 6px;
    border: 1px solid #ebeef5;
    background-color: #fff;
    overflow: hidden;
    color: #303133;
  }
  .btn-wrap {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
  }
}

@media (max-width: 768px) {
  .chart-container {
    width: 96%;
